





















import {
  defineComponent,
  ref,
  watch,
  toRefs,
  computed,
  Ref,
  onMounted,
} from '@vue/composition-api'

export default defineComponent({
  props: {
    multiline: {
      type: Boolean,
      default: false,
    },
    value: {
      type: String,
      default: '',
    },
    error: {
      type: Boolean,
      default: false,
    },
    autoExpand: {
      type: Boolean,
      default: false,
    },
    baseTextareaHeight: {
      type: Number,
      default: 56,
    },
  },
  setup(props, context) {
    const inputListeners = computed(() => {
      return Object.assign({}, context.listeners, {
        input: (e: { target: HTMLInputElement }) => {
          context.emit('input', e.target.value)
        },
      })
    })

    const textarea = ref<HTMLElement | null>(null)
    const resizeTextareaIfAutoExpand = () => {
      if (textarea.value && props.autoExpand) {
        textarea.value.style.height = ''
        textarea.value.style.height = `${Math.max(
          textarea.value.scrollHeight,
          props.baseTextareaHeight
        )}px`
      }
    }

    const { value } = toRefs(props)
    watch(value, () => {
      resizeTextareaIfAutoExpand()
    })

    onMounted(() => {
      resizeTextareaIfAutoExpand()
    })

    return {
      context,
      textarea,
      inputListeners,
    }
  },
})
